import * as React from "react";
import Layout from "../components/layout";
import HeroAnimation from "../components/home/heroAnimation";
import HeroProductOverview from "../components/home/heroProductOverview";
import BlogOverview from "../components/home/blogOverview";

const heroStyles = {
  height: "50vh",
};

const IndexPage = () => {
  return (
    <Layout pageTitle="Home Page">
      <HeroAnimation>
        <div className="text-center vh-100">
          <p className="fs-1">Lane 7 Animation</p>
        </div>
      </HeroAnimation>
      <HeroAnimation>
        <div className="text-center bg-dark text-white vh-100">
          <p className="fs-1 p-4">Imagine Color</p>
        </div>
      </HeroAnimation>
      <HeroAnimation>
        <div className="text-center p-2 vh-50">
          <p className="fs-1 p-4">Welcome to Lane7</p>
          <p className="fs-2 p-4">
            An independent agency dedicated to the craft of advertising
          </p>
        </div>
      </HeroAnimation>
      <HeroAnimation>
        <div className="text-center bg-dark text-white vh-50">
          <p className="py-4">FEATURED WORK</p>
          <p className="py-2">BUTTON</p>
          <p style={{ fontSize: "2em" }} className="py-4">
            Concepts to Ideas to Brands Animation
          </p>
        </div>
      </HeroAnimation>
      <HeroAnimation>
        <HeroProductOverview isPage="index" />
      </HeroAnimation>
      <BlogOverview />
      <HeroAnimation>
        <div className="text-center border-top bg-dark text-white vh-50">
          <p className="py-4 fs-1">Want to work together</p>
          <button
            type="button"
            className="btn btn-warning btn-lg mb-4 fw-bold rounded-pill fs-4"
          >
            Contact us
          </button>
        </div>
      </HeroAnimation>
    </Layout>
  );
};

export default IndexPage;

export const Head = () => <title>Home Page</title>;
