const BlogOverviewItem = ({ item, children }) => {
  return (
    <div className="col-md-4 col-sm-12">
      <svg
        className="img-fluid"
        height="250"
        xmlns="http://www.w3.org/2000/svg"
        role="img"
        //ariaLabel=""
        //preserveAspectRatio="xMidYM   id slice"
        // focusable="false"
      >
        <rect width="100%" height="100%" fill="#868e96"></rect>
      </svg>
      <p className="pt-2 fw-bold">{item.title}</p>
      <p className="fs-2 pt-1 text-wrap fw-bold">{item.desc}</p>
    </div>
  );
};

export default BlogOverviewItem;
